body {
  font-size: 14px;
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  padding: 0;
  margin: 0;
}

img {
  width: 100%;
  display: block;
}

p,
ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
}

@media (min-width: 320px) {
  .container {
    min-width: 320px;
  }
}
@media (min-width: 768px) {
  .container {
    width: 700px;
  }
}
@media (min-width: 1200px) {
  .container {
    width: 1040px;
  }
}
.about {
  background-color: #fff5f6;
  padding-bottom: 145px;
}

.heading {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 0 auto 26px;
  padding-top: 125px;
}
.heading__secondary {
  font-family: "Titan One";
  font-style: normal;
  font-weight: 400;
  text-align: center;
  text-transform: uppercase;
  color: #ffa5ba;
  margin-bottom: 15px;
}
@media screen and (max-width: 768px) {
  .heading__secondary {
    font-size: 18px;
    line-height: 0.857;
  }
}
.heading__main {
  font-family: "Titan One";
  font-style: normal;
  font-weight: 400;
  letter-spacing: 0.04em;
  text-align: center;
  text-transform: uppercase;
  color: #d41443;
  margin: 0 auto;
}
@media screen and (max-width: 768px) {
  .heading__main {
    font-size: 34px;
    line-height: 1.147;
  }
}

.about-description {
  min-width: 280px;
}
.about-description__image {
  min-width: 280px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}
.about-description__image-item {
  height: 100%;
}
.about-description__text {
  padding: 0 50px;
  font-family: "DM Sans";
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.62;
}
.about-description__text--main {
  margin-top: 46px;
  margin-bottom: 47px;
  color: #000;
}
.about-description__text--secondary {
  margin-bottom: 47px;
  color: #907e82;
}
@media screen and (max-width: 768px) {
  .about-description__text--secondary {
    z-index: 2;
    position: relative;
    background-size: cover;
    background-repeat: no-repeat;
  }
}
.button__vector {
  margin-left: 13.62px;
}

.flex-center {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}

.about-achievements {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  margin-top: 39px;
}
.about-achievements__items {
  max-width: 230px;
}
.about-achievements__items:not(:last-child) {
  margin-bottom: 38px;
}
.about-achievements__list {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.about-achievements__icon {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin-bottom: 16px;
}
.about-achievements__numbers {
  font-family: "Titan One";
  font-style: normal;
  font-weight: 400;
  letter-spacing: 0.04em;
  text-align: center;
  text-transform: uppercase;
  color: #d41443;
  text-align: left;
  font-size: 36px;
  line-height: 0.878;
  height: 56px;
}
.about-achievements__description {
  font-family: "DM Sans";
  font-weight: 700;
  font-size: 12px;
  line-height: 1.7;
  color: #000;
}

@media screen and (min-width: 768px) {
  .about {
    padding-bottom: 140px;
  }
  .heading {
    margin-bottom: 50px;
    padding-top: 130px;
  }
  .heading__secondary {
    font-size: 26px;
    line-height: 0.866;
  }
  .heading__main {
    font-size: 54px;
    line-height: 1.145;
  }
  .about-description {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    margin: 0 auto;
  }
  .about-description__image {
    align-self: flex-start;
    width: 340px;
    height: 339px;
  }
  .about-description__text {
    padding-left: 17px;
    padding-right: 0px;
    width: 340px;
    font-size: 14px;
    line-height: 1.76;
  }
  .about-description__text--main {
    margin-bottom: 49px;
  }
  .about-description__text--secondary {
    margin-bottom: 62px;
  }
  .about-achievements__list {
    flex-direction: row;
    align-items: flex-start;
    flex-wrap: nowrap;
    gap: 60px;
  }
  .about-achievements__items {
    width: 180px;
  }
  .about-achievements__icon {
    margin-bottom: 36px;
  }
  .about-achievements__numbers {
    font-size: 48px;
    line-height: 1.145;
  }
  .about-achievements__description {
    margin-top: 13px;
    font-size: 14px;
    line-height: 1.74;
  }
}
@media screen and (min-width: 1200px) {
  .heading__secondary {
    font-size: 30px;
    line-height: 0.882;
  }
  .heading__main {
    font-size: 58px;
    line-height: 0.878;
  }
  .about-description {
    z-index: 2;
    position: relative;
  }
  .about-description__image {
    width: 516px;
    height: 516px;
  }
  .about-description__text {
    margin-left: 74px;
    width: 463px;
  }
  .flex-center {
    justify-content: flex-start;
  }
  .about-achievements {
    gap: 30px;
  }
  .about-achievements__items {
    width: 330px;
  }
  .about-achievements__numbers {
    font-size: 58px;
  }
}
.button {
  border-radius: 22px;
  width: 176px;
  height: 44px;
  cursor: pointer;
  font-family: "DM Sans";
  font-weight: 700;
  font-size: 14px;
  line-height: 0.78;
  display: flex;
  align-items: center;
  justify-content: center;
  border-color: transparent;
}
.button--bright {
  background: #fff;
  color: #d41443;
  fill: #d41443;
}
.button--color {
  background: #d41342;
  color: #fff;
}

.products__title {
  text-align: center;
}
.products__secondheader {
  font-family: "Titan One";
  font-style: normal;
  font-weight: 400;
  text-align: center;
  text-transform: uppercase;
  color: #ffa5ba;
}
.products__header {
  font-family: "Titan One";
  font-style: normal;
  font-weight: 400;
  letter-spacing: 0.04em;
  text-align: center;
  text-transform: uppercase;
  color: #d41443;
}
.products__item {
  position: relative;
  width: 280px;
  height: 485px;
  border-radius: 24px;
  margin-bottom: 130px;
}
.products__item--icecream {
  background-color: #ffb8ca;
}
.products__item--icecoffe {
  background-color: #f0d1a5;
}
.products__item--milkshakes {
  background-color: #c2e297;
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .products__title {
    margin-top: 144px;
  }
  .products__secondheader {
    font-size: 18px;
    line-height: 0.857;
  }
  .products__header {
    font-size: 34px;
    line-height: 1.147;
    margin-bottom: 144px;
  }
  .products__list {
    align-items: center;
    flex-direction: column;
    display: flex;
  }
  .products__name {
    font-family: "Titan One";
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 1.15;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: #ffffff;
    margin-top: 161px;
  }
  .products__name::after {
    content: ". . .";
    font-weight: 700;
    font-size: 20px;
    display: block;
    margin: 30px 0px;
  }
  .products__decription {
    font-family: "DM Sans";
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 1.52;
    text-align: center;
    letter-spacing: 0.04em;
    color: #ffffff;
    margin-left: 41px;
    margin-right: 39px;
  }
}
.gallery {
  display: flex;
  justify-content: center;
}

.gallery img {
  width: 100%;
  height: 100%;
  max-width: 1280px;
  max-height: 430px;
}

footer {
  background-color: #fff5f6;
  width: 100%;
}

.footer {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.footer__container {
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}
.footer__tittle {
  font-family: Titan One;
  font-size: 30px;
  font-weight: 400;
  line-height: 1.133;
  letter-spacing: 0.04em;
  text-align: left;
  color: #ffa5ba;
  text-transform: uppercase;
}
.footer__text {
  font-family: Titan One;
  font-size: 58px;
  font-weight: 400;
  line-height: 1.137;
  letter-spacing: 0.06em;
  text-align: left;
  color: #d41443;
  text-transform: uppercase;
}
.footer__icons {
  display: flex;
  gap: 24px;
}
.footer__icon {
  text-decoration: none;
  width: 34px;
  height: 34px;
  background-color: #ffa5ba;
  border-radius: 50%;
  fill: #fff;
  transition: 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
.footer__icon:hover,
.footer __icon :focus {
  color: blueviolet;
}
.footer__contact {
  font-family: DM Sans;
  font-size: 16px;
  font-weight: 700;
  line-height: 30px;
  letter-spacing: 0.02em;
  text-align: left;
  font-family: DM Sans;
  font-size: 16px;
  font-weight: 500;
  line-height: 30px;
  letter-spacing: 0.02em;
  text-align: left;
  color: #000;
}

.contacts {
  background-color: #fff5f6;
}
.contacts__card {
  width: 100%;
  background-color: #fff;
  box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
  border-radius: 24px;
  width: 220px;
  margin: 0 auto;
  margin-bottom: 20px;
  padding: 43px 20px 43px 20px;
}
.contacts__category {
  display: inline-block;
  background-color: #f0d1a5;
  padding: 0px 20px 0px 20px;
  margin-bottom: 25px;
  border-radius: 5px;
  color: white;
  font-family: "DM Sans";
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.86;
}
.contacts__category--foodtruck {
  background-color: #c2e297;
}
.contacts__list {
  display: flex;
  flex-wrap: wrap;
}
.contacts__city {
  font-family: "DM Sans";
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 186%;
  letter-spacing: 0.04em;
  color: black;
  padding-bottom: 10px;
}
.contacts__desc {
  font-family: "DM Sans";
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 18px;
  color: #907e82;
  padding-bottom: 25px;
  border-bottom: #e1e1e1 solid 1px;
  margin-bottom: 25px;
}
.contacts__days {
  font-family: "DM Sans";
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.86;
}
.contacts__hours {
  font-family: "DM Sans";
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.86;
  color: #ffa5ba;
  margin-bottom: 10px;
}
.contacts__hours--bottom {
  padding-bottom: 25px;
  border-bottom: #e1e1e1 solid 1px;
  margin-bottom: 25px;
}
.contacts__address {
  font-family: "DM Sans";
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 18px;
  color: #907e82;
}

.header {
  background-color: #ffb8ca;
  padding: 6px 20px 0 20px;
  height: 502px;
  min-width: 320px;
}
.header__container {
  display: none;
}
.header__primary--image {
  position: absolute;
  z-index: 2;
  top: 92px;
  right: 32px;
  height: 410px;
  width: 171px;
}
.header__secondary--image {
  display: none;
}
.header__tertiary--image {
  display: none;
}

.box {
  display: none;
}

.menu {
  min-width: 320px;
}
.menu__logo {
  width: 199px;
  height: 50px;
}
.menu__list {
  display: none;
}
.menu__button {
  display: none;
}
.menu__button:hover {
  background-color: #ecebeb;
  cursor: pointer;
}

.block {
  display: flex;
  flex-direction: column;
  margin: 137px 0 141px 0;
  max-width: 218px;
  gap: 19px;
  line-height: 35px;
}
.block__header {
  margin: 0;
  z-index: 3;
  top: 0;
}
.block__header-lower {
  font-family: "Titan One";
  font-style: normal;
  font-weight: 400;
  letter-spacing: 0.04em;
  text-align: center;
  text-transform: uppercase;
  color: #d41443;
  font-size: 30px;
  color: #fff;
}
.block__header-higher {
  font-family: "Titan One";
  font-style: normal;
  font-weight: 400;
  letter-spacing: 0.04em;
  text-align: center;
  text-transform: uppercase;
  color: #d41443;
  font-size: 48px;
}
.block__box {
  display: flex;
  gap: 8px;
  z-index: 3;
}
.block__button-first, .block__button-second {
  height: 24px;
  font-family: "DM Sans";
  font-size: 8px;
  padding: 12px;
  display: flex;
  align-items: center;
}
.block__button-first {
  background-color: #d41443;
  color: #fff;
}
.block__button-first:hover {
  background-color: #c7133e;
  cursor: pointer;
}
.block__button-second {
  background-color: #fff;
  color: #d41443;
}
.block__button-second:hover {
  background-color: #ecebeb;
  cursor: pointer;
}

.circle {
  width: 345px;
  height: 345px;
  background-color: #ffa5ba;
  position: absolute;
  border-radius: 50%;
  top: 83px;
  right: -62px;
}

.icon--circle {
  display: none;
}

.icon--arrow {
  display: none;
}

.backdrop {
  display: flex;
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-color: hsla(0deg, 0%, 0%, 0.5);
  transition: ease-in-out 500ms;
  z-index: 6;
}

.modal {
  width: 528px;
  min-height: 581px;
  background-color: #ffb8ca;
  margin: auto;
  padding: 0;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14), 0px 2px 1px rgba(0, 0, 0, 0.2);
  border-radius: 20px;
}
.modal__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 15px;
  gap: 10px;
}
.modal__button {
  display: block;
  width: 160px;
  height: 44px;
  background-color: #fff;
  font-family: "DM Sans";
  font-weight: 700;
  font-size: 16px;
  line-height: 21px;
  color: #d41443;
  position: relative;
  top: 10px;
  right: -360px;
}
.modal__button:hover {
  cursor: pointer;
  background-color: #ecebeb;
}
.modal__header {
  font-family: "DM Sans";
  font-weight: 700;
  font-size: 22px;
  color: #fff;
  line-height: 1.3;
}
.modal__label {
  display: flex;
  flex-direction: column;
  gap: 5px;
  font-family: "DM Sans";
  font-weight: 700;
  font-size: 13px;
  color: #fff;
  line-height: 1.3;
}
.modal__input {
  width: 320px;
  height: 30px;
  border-radius: 10px;
  padding-left: 10px;
  font-family: "DM Sans";
  font-weight: 700;
  font-size: 13px;
  color: #d41443;
  line-height: 1.3;
}
.modal__textarea {
  width: 320px;
  height: 150px;
  border-radius: 10px;
  padding-left: 10px;
  padding-top: 10px;
  font-family: "DM Sans";
  font-weight: 700;
  font-size: 13px;
  color: #d41443;
  line-height: 1.3;
}
.modal__acceptance-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 15px;
  padding-top: 10px;
}
.modal__acceptance-label {
  font-family: "DM Sans";
  font-weight: 700;
  font-size: 13px;
  color: #fff;
  line-height: 1.3;
  letter-spacing: 0.03em;
}
.modal__acceptance-input {
  margin-right: 5px;
}
.modal__link {
  color: #d41443;
}
.modal__submit {
  width: 160px;
  height: 44px;
  background-color: #c2e297;
  font-family: "DM Sans";
  font-weight: 700;
  font-size: 16px;
  line-height: 21px;
  color: #fff;
}
.modal__submit:hover {
  cursor: pointer;
  background-color: #fff;
  color: #d41443;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

